@import url("https://fonts.googleapis.com/css?family=Red+Hat+Display&display=swap");

* {
    box-sizing: border-box;
    font-family: "Red Hat Display";
    margin: 0;
    color: white;
    text-shadow: 0.03em 0.03em 0 black;
}

body {
    height: 100%;
    background-color: lightslategray;
    /* background-image: url("https://images.pexels.com/photos/2312040/pexels-photo-2312040.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940"); */
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
}

.loading {
    text-align: center;
    font-size: 4rem;
    font-weight: bold;
    color: #fff;
}

.app {
    text-align: center;
}

.home-page-header {
    font-size: 5rem;
    display: flex;
    align-items: center;
    justify-content: center;
}

.artist-counter {
    color: #fff;
    font-size: 2rem;
    margin: 2px;
    display: inline-block;
    text-align: center;
}

.filter {
    color: #fff;
    font-size: 1.2rem;
    margin: 1%;
    padding-left: 1%;
    padding-right: 1%;
    display: flex;
    justify-content: center;
}

.filter span {
    padding-right: 1%;
}

.filter-input {
    background-color: #fff;
    color: black;
    text-shadow: none;
}

ul {
    padding: 0;
}

li {
    display: inline-block;
}

.album-content {
    background-color: rgba(255, 255, 255, 0.3);
    margin: 5px;
    display: inline-flex;
}

.description {
    width: 250px;
    display: grid;
    align-self: center;
    gap: 2rem;
}

.artist-name {
    font-size: 1.5rem;
    font-weight: bold;
}

.album-title {
    font-size: 1.5rem;
    font-weight: normal;
}

.release-date {
    font-size: 1.1rem;
    font-weight: normal;
}

.album-cover {
    width: 315px;
    height: 315px;
}

.open-in-spotify-btn {
    font-size: 1.2rem;
    color: black;
    text-shadow: none;
    background-color: #1ed761;
    margin-top: 10px;
}

.open-in-spotify-btn:hover {
    opacity: 0.8;
    cursor: pointer;
}

.sign-in-button {
    display: inline-block;
    font-size: 2rem;
    color: black;
    text-shadow: none;
    background-color: #1ed761;
    border-color: black;
    margin: 10px;
}

@media screen and (max-width: 600px) {
    .home-page-header {
        font-size: 2.5rem;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    
    .artist-counter {
        color: #fff;
        font-size: 1.5rem;
        margin: 2px;
        display: inline-block;
        text-align: center;
    }

    .filter {
        color: #fff;
        font-size: 1rem;
        margin: 1%;
        padding-left: 1%;
        padding-right: 1%;
        display: flex;
        justify-content: center;
    }
    
    .filter span {
        padding-right: 1%;
    }
    
    .filter-input {
        background-color: #fff;
        color: black;
        text-shadow: none;
        width: 50%;
    }
    
    .album-content {
        background-color: rgba(255, 255, 255, 0.3);
        margin: 5px;
        display: inline-flex;
    }
    
    .description {
        width: 175px;
        display: inline-block;
    }
    
    .artist-name {
        font-size: 1rem;
        font-weight: bold;
        margin: 0;
    }
    
    .album-title {
        font-size: 1rem;
        font-weight: normal;
        margin: 0;
    }
    
    .release-date {
        font-size: 0.8rem;
        font-weight: normal;
        margin: 0;
    }
    
    .album-cover {
        width: 150px;
        height: 150px;
    }
    
    .open-in-spotify-btn {
        font-size: 0.8rem;
        color: black;
        text-shadow: none;
        background-color: #1ed761;
    }
}
